@import url(https://fonts.googleapis.com/css2?family=Poppins&family=Work+Sans:wght@400;500&family=Karla&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/static/media/background.0759d41b.png);
  background-size: 250px;
  background-repeat: repeat;
  background-attachment: fixed;
  background-color: #161920;
  color: #ffffff;
}

svg.top {
  position: absolute;
  bottom: -8vw;
  width: 100%;
  height: 8vw;
}
